"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import "./styles.css";

// Mock leaderboard data based on the image
const mockLeaderboardData = [
  {
    rank: 1,
    username: "Eru",
    xp: 10500,
    ranking: "Ascendant",
    badge: "ascendant"
  },
  {
    rank: 2,
    username: "Yuri",
    xp: 10100,
    ranking: "Peak Performer I",
    badge: "peak-performer-1"
  },
  {
    rank: 3,
    username: "Bella",
    xp: 9000,
    ranking: "Peak Performer III",
    badge: "peak-performer-3"
  },
  {
    rank: 4,
    username: "PSUIA",
    xp: 7900,
    ranking: "Mastery I",
    badge: "mastery-1"
  },
  {
    rank: 5,
    username: "Al",
    xp: 5200,
    ranking: "Self-Driven I",
    badge: "self-driven-1"
  },
  {
    rank: 6,
    username: "IA",
    xp: 3300,
    ranking: "Growth Seeker I",
    badge: "growth-seeker-1"
  },
  {
    rank: 7,
    username: "TechNinja",
    xp: 2800,
    ranking: "Growth Seeker III",
    badge: "growth-seeker-3"
  },
  {
    rank: 8,
    username: "CodeMaster",
    xp: 2100,
    ranking: "Growth Seeker V",
    badge: "growth-seeker-5"
  },
  {
    rank: 9,
    username: "StudyBuddy",
    xp: 1700,
    ranking: "Habit Builder II",
    badge: "habit-builder-2"
  },
  {
    rank: 10,
    username: "QuizPro",
    xp: 1200,
    ranking: "Habit Builder IV",
    badge: "habit-builder-4"
  }
];

export default function LeaderboardPage() {
  const [leaderboard, setLeaderboard] = useState([]);
  const [currentUser, setCurrentUser] = useState("Eru"); // Mock current user

  useEffect(() => {
    // In a real app, this would fetch from the API
    setLeaderboard(mockLeaderboardData);
  }, []);

  const top3 = leaderboard.slice(0, 3);
  const rest = leaderboard.slice(3);

  const getMedalIcon = (rank) => {
    switch (rank) {
      case 1:
        return "ü•á";
      case 2:
        return "ü•à";
      case 3:
        return "ü•â";
      default:
        return null;
    }
  };

  const formatXP = (xp) => {
    if (xp >= 1000) {
      return `${(xp / 1000).toFixed(1)}k XP`;
    }
    return `${xp} XP`;
  };

  const getBadgeColor = (badge) => {
    if (badge.includes("ascendant")) return "#FF1493";
    if (badge.includes("peak-performer")) return "#00CED1";
    if (badge.includes("mastery")) return "#9370DB";
    if (badge.includes("self-driven")) return "#90EE90";
    if (badge.includes("growth-seeker")) return "#FFD700";
    if (badge.includes("habit-builder")) return "#C0C0C0";
    return "#CD7F32";
  };

  return (
    <div className="leaderboard-container">
      {/* Header */}
      <div className="leaderboard-header">
        <div className="search-bar-wrapper">
          <input
            type="text"
            placeholder="Search"
            className="search-input"
          />
          <span className="search-icon">üîç</span>
        </div>
        
        <div className="user-actions">
          <button className="notification-btn">üîî</button>
          <div className="user-profile">
            <div className="user-avatar">üë§</div>
            <div className="user-info">
              <span className="user-name">Eru.</span>
              <span className="user-role">Student</span>
            </div>
          </div>
        </div>
      </div>

      {/* Main Content */}
      <div className="leaderboard-content">
        <h1 className="leaderboard-title">LEADERBOARD</h1>

        {/* Top 3 Podium */}
        <div className="podium-container">
          {/* 2nd Place */}
          {top3[1] && (
            <div className="podium-card podium-2nd">
              <div className="medal-badge silver-medal">
                <span className="medal-text">2ND</span>
              </div>
              <div className="podium-avatar">üë§</div>
              <h3 className="podium-username">{top3[1].username}</h3>
              <p className="podium-xp">{formatXP(top3[1].xp)}</p>
              <div className="podium-rank-badge" style={{ backgroundColor: getBadgeColor(top3[1].badge) }}>
                <span className="rank-badge-icon">üèÜ</span>
                <span className="rank-badge-text">{top3[1].ranking}</span>
              </div>
            </div>
          )}

          {/* 1st Place */}
          {top3[0] && (
            <div className="podium-card podium-1st">
              <div className="medal-badge gold-medal">
                <span className="medal-text">1ST</span>
              </div>
              <div className="podium-avatar">üë§</div>
              <h3 className="podium-username">{top3[0].username}</h3>
              <p className="podium-xp">{formatXP(top3[0].xp)}</p>
              <div className="podium-rank-badge" style={{ backgroundColor: getBadgeColor(top3[0].badge) }}>
                <span className="rank-badge-icon">üèÜ</span>
                <span className="rank-badge-text">{top3[0].ranking}</span>
              </div>
            </div>
          )}

          {/* 3rd Place */}
          {top3[2] && (
            <div className="podium-card podium-3rd">
              <div className="medal-badge bronze-medal">
                <span className="medal-text">3RD</span>
              </div>
              <div className="podium-avatar">üë§</div>
              <h3 className="podium-username">{top3[2].username}</h3>
              <p className="podium-xp">{formatXP(top3[2].xp)}</p>
              <div className="podium-rank-badge" style={{ backgroundColor: getBadgeColor(top3[2].badge) }}>
                <span className="rank-badge-icon">üèÜ</span>
                <span className="rank-badge-text">{top3[2].ranking}</span>
              </div>
            </div>
          )}
        </div>

        {/* Leaderboard Table */}
        <div className="leaderboard-table">
          <div className="table-header">
            <div className="table-col col-rank">Rank</div>
            <div className="table-col col-username">Username</div>
            <div className="table-col col-ranking">Ranking</div>
            <div className="table-col col-xp">XP Points</div>
          </div>

          <div className="table-body">
            {rest.map((player) => (
              <div
                key={player.rank}
                className={`table-row ${player.username === currentUser ? "current-user" : ""}`}
              >
                <div className="table-col col-rank">
                  <div className="rank-circle">{player.rank}</div>
                </div>
                <div className="table-col col-username">
                  <div className="username-cell">
                    <div className="user-avatar-small">üë§</div>
                    <span className="username-text">{player.username}</span>
                  </div>
                </div>
                <div className="table-col col-ranking">
                  <div className="ranking-badge" style={{ backgroundColor: getBadgeColor(player.badge) }}>
                    <span className="ranking-icon">üèÜ</span>
                    <span className="ranking-text">{player.ranking}</span>
                  </div>
                </div>
                <div className="table-col col-xp">
                  <span className="xp-text">{formatXP(player.xp)}</span>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}
